<template>
    <div class="app-container" v-loading="isLoad" >
    
        <el-form :label-position="labelPosition" label-width="80px" :model="form" ref='form' style="width:500px ;margin:0 auto;" :rules="rules">
            <el-form-item label="合同编号:" prop="contractNumber">
                <el-input v-model.number="form.contractNumber" disabled></el-input>
            </el-form-item>
            <el-form-item label="卡管理费(%):" prop="ratio2" >
                <el-input v-model="form.ratio2" disabled></el-input>
            </el-form-item>
            <el-form-item label="合同时间范围:" prop="range" >
                <el-date-picker v-model="form.range" type="daterange" disabled>
                </el-date-picker>
            </el-form-item>
    
        </el-form>
    </div>
</template>

<script>
/* eslint-disable quotes,indent,keyword-spacing,spaced-comment,no-var */

import {
    getCTRatio
} from 'api/admin/basicData';
import { backCN } from 'utils/index'
import { mapActions } from 'vuex'
export default {
    data() {
        return {
            labelPosition: 'left',
            itemList: [],
            form: {
                id: '',
                contractNumber: '',
                ratio2: '',
                range: undefined
            },
            rules: {
                // contractNumber: [ { required: true, message: '请输入内容', trigger: 'blur' }
                // ,{ type: 'number', message: '请输入数字值'}],
                // ratio1: [ { required: true, message: '请输入内容', trigger: 'blur' }
                // ,{ type: 'number', message: '请输入数字值'}],
                // ratio2: [ { required: true, message: '请输入内容', trigger: 'blur' }
                // ,{ type: 'float', message: '请输入数字值'}],
                // range: [ { required: true, message: '请输入内容', trigger: 'blur' }],
            }
        }
    },
    computed: {
        isLoad() {
            return this.$store.state.loading.load
        }
    },
    watch: {},
    mounted() {
        this.CTInfo();
    },
    methods: {
        ...mapActions(["setLoad"]),
        CTInfo(ctId) {
            this.isLoad=true
            getCTRatio()
                .then(res => {
                    this.isLoad=false
                    if (res.msg == 0) {
                        console.log("res1", res)
                        this.form.contractNumber = Number(res.contractNumber);
                        this.form.ratio1 = res.value;
                        this.form.range = [res.startTime, res.endTime]
                    }
                });


        }
    }
};
</script>

<style scoped>
.app-container {
    background: #f5f5f5;
}

.clearfix:before,
.clearfix:after {
    display: table;
    content: "";
}

.clearfix:after {
    clear: both
}

.box-card {
    width: 100%;
    min-width: 637px;
    margin-bottom: 20px;
}

.survey-container {}

.survey-item-wrap {
    padding: 5px 0;
}

.survey-item {
    border-right: 1px solid #e0e0e0;
}

.survey-item-wrap:last-child .survey-item {
    border: none;
}

.survey-item h3 {
    font-size: 13px;
    color: #666;
}

.survey-item p {
    color: #f88b00;
    font-size: 25px;
}

.content-rowspan div {
    padding-left: 13px;
    line-height: 46px;
    border-bottom: 1px solid #ECEDEE;
}

.extend .cell {
    padding: 0;
}

.flow-details {
    border-bottom: 1px solid #e8e8e8;
}

.flow-details:last-child {
    border: none;
}

.item-left {
    padding: 7px 0;
}
</style>


